  AJAX_NOT_FOUND: "Error Loading AJAX : Not Found",
  CLOSE: "",
  DOWNLOAD: "",
  ELEMENT_NOT_FOUND: "HTML-  ",
  ERROR: "    ,  ",
  FITX: "Fit horizontally",
  FITY: "Fit vertically",
  FLIPX: "Flip horizontally",
  FLIPY: "Flip vertically",
  IFRAME_ERROR: "  ",
  IMAGE_ERROR: "  ",
  ITERATEZOOM: "Toggle zoom level",
  MODAL: "         ESC",
  NEXT: "",
  PANDOWN: "Move down",
  PANLEFT: "Move left",
  PANRIGHT: "Move right",
  PANUP: "Move up",
  PREV: "",
